/*Color de fondo que tendra el navbar. Este cambiaria en otros monitores al usa media querys*/
.navbar-light{
  background-color: #2e4053;  
}
/*color por defecto del logo o texto*/
.navbar-light .navbar-brand{
  color: #76B900;
}
/*Cambios al interactuar con el mouse*/
.navbar-light .navbar-brand:hover,
.navbar-light .navbar-brand:focus,
.navbar-light .navbar-brand:active,
.navbar-light .navbar-brand.active {
  color: #A2C63C;
}
/*boton en modo movil*/
.navbar-light .navbar-toggler{
  border-color: #fff;/*color de borde blanco*/
  background-color: #76B900;/*color de fondo en verde mas claro*/
}

/*color de barras blanca en boton en modo movil*/
.navbar-light .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255,255,255)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
}

.navbar-light .navbar-toggler:hover,
.navbar-light .navbar-toggler:focus {
    background-color: #5D9200;
}
/*Enlaces del navbar*/
.navbar-light .navbar-nav .nav-item a {
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 1px;
    color: #fff;
    font-size: 14px;
}
/*Cambios al interactuar con el mouse*/
.navbar-light .navbar-nav .nav-item a:hover,
.navbar-light .navbar-nav .nav-item a:focus,
.navbar-light .navbar-nav .nav-item a:active{
  outline: 0;/* propiedad usada para crear un segundo borde */
  color: #76B900;
}

/*Color de fondo de dropdown*/
.navbar-light .dropdown-menu {
   background-color: #222;/*El enlace toma el color negro*/   
}
/*Cambios al dar clic sobre enlaces de dropdown*/
.dropdown-item:hover, .dropdown-item:focus {
  color: #16181b;
  text-decoration: none;
  background-color: #1C2833;
}

/*Linea divisora de enlaces del dropdown*/
.dropdown-divider {
  border-top: 1px solid #5D9200;
}
/*Comportamiento del navbar en pantallas con resoluciones minima de 768*/
@media(min-width:768px) { 
    
    .fixed{      
      position: fixed; /*Se mantine fijada la barra de menu solo en pantallas mayores a 768*/
      top: 0;
      right: 0;
      left: 0;
      z-index: 1030;
    }
    .navbar-light {
        padding: 9px 0;/*margenes superior e inferior de 18px y 0px para derecho e izquierdo del nav*/
        border: 0;
        background-color: #34495e;/*Color de fondo*/
       /* background-color: transparent;/*Cuando se desea transparente*/
        -webkit-transition: padding .3s;/*Compatibilidad para safary*/
        -moz-transition: padding .3s;/*Compatible para nevegador chrome*/
        transition: padding .3s;/*Compatible para Mozilla*/
        /*Cambio de transicion de Padding sera de .3s, tiempo de transicion desde posicion inicial al final*/

    }
    /*TamaÃ±o por defecto del texto del logotipo*/
    .navbar-light .navbar-brand {
        font-size: 1.3em;/*TamaÃ±o de fuente*/
        -webkit-transition: all .3s;
        -moz-transition: all .3s;
        transition: all .3s;
    }
      
    /*Cambio de tamaÃ±o que tendra el navbar al desplazarse hacia abajo de paginas*/
    .navbar-light.navbar-shrink {
        padding: 3px 0;/*separacion del borde superior*/
        background-color: #2e4053;/*fondo de color negro*/
    }
    /*Cambio de tamaÃ±o que tendra el texto del logotipo al desplazarse hacia abajo de paginas*/
    .navbar-light.navbar-shrink .navbar-brand {
        font-size: 1.0em;
        color:#558b2f;
    }
    /*Cambio de color de la barra de navegacion*/
    .navbar-light.navbar-shrink{
        background-color:#d5d8dc;  
    }
    /*Cambio de color de enlaces del navbar*/
    .navbar-light.navbar-shrink .navbar-nav .nav-item a {   
        color: #558b2f;
        font-weight: 800;
    }
}
