/*Comportamiento normal del header. Puede cambiar segun resolucion en los media querys*/
header{
  text-align: center;/*Texto alineado al centro*/
    color: #ffc107 ;/*Color del texto*/
    font-weight: 800;
    /*background-attachment: scroll;/*La imagen de fondo se moverÃ¡ dentro de la pantalla*/
    background-image: url(../images/header.jpg);/*Se define la imagen de fondo*/
    background-position: center center;/*define la posiciÃ³n de imagen de fondo centrada*/
    background-repeat: no-repeat; /*Fondo de imagen puede ser repetido sobre el eje horizontal, el eje vertical, ambos ejes, o no estar repetido.*/
    background-size: cover;/*cover redimensiona la imagen al mayor tamaÃ±o posible sin estirarla.*/
    -webkit-background-size: cover;/*Compatibilidad para chrome*/
    -moz-background-size: cover;/*Compatilidad con firefox*/    
    -o-background-size: cover;/*Compatibiliad con opera*/
}
/*Comportamiento normal de contenedor dentro del header*/
header .intro-text{ 
  padding-top: 95px;/*Espacio de relleno en la parte superior*/
  padding-bottom: 40px;/*Espacio de relleno en la parte inferior*/
}
/*Primer texto dentro de contenedor*/
header .intro-text .intro-lead-in {
    margin-bottom: 20px;/*margen-inferior*/
    font-size: 30px;
    font-style: italic;
    line-height: 22px;    
}
/*Segundo texto dentr del contenedor acerca de mi*/
header .intro-text .intro-heading {
    margin-bottom: 25px;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 700;
    line-height: 22px;
}

/*Comportamiento del header para monitores con resolucion minima de 768px*/
@media(min-width:768px) {
    header .intro-text {
        padding-top: 250px;
        padding-bottom: 200px;
    }

    header .intro-text .intro-lead-in {
        margin-bottom: 20px;
        font-size: 45px;
        font-style: italic;
        line-height: 40px;
    }

    header .intro-text .intro-heading {
        margin-bottom: 50px;
        text-transform: uppercase;
        font-size: 35px;
        font-weight: 700;
        line-height: 75px;
    }
}

